// Socket.IO context provider for the application
import React, {
	createContext,
	useContext,
	useEffect,
	useState,
	ReactNode,
} from 'react';

// Note: socket.io-client would be an optional dependency
// This provider supports both WebSocket and Socket.IO depending on what's available
interface SocketContextType {
	socket: any | null; // Could be Socket.IO Socket or WebSocket
	isConnected: boolean;
	error: string | null;
	connect: (url?: string) => void;
	disconnect: () => void;
	emit?: (event: string, data?: any) => void;
}

export const SocketContext =
	createContext<SocketContextType>({
		socket: null,
		isConnected: false,
		error: null,
		connect: () => {},
		disconnect: () => {},
	});

export const useSocket = () => {
	const context = useContext(SocketContext);
	if (!context) {
		throw new Error(
			'useSocket must be used within a SocketProvider'
		);
	}
	return context;
};

interface SocketProviderProps {
	children: ReactNode;
	session?: any; // Optional session object
	autoConnect?: boolean;
	url?: string;
}

const SocketProvider: React.FC<SocketProviderProps> = ({
	children,
	session,
	autoConnect = false,
	url = '/api/socket',
}) => {
	const [socket, setSocket] = useState<any>(null);
	const [isConnected, setIsConnected] = useState(false);
	const [error, setError] = useState<string | null>(null);

	const connect = (connectUrl?: string) => {
		try {
			const targetUrl = connectUrl || url;

			// Try to use Socket.IO if available (for enhanced features)
			if (
				typeof window !== 'undefined' &&
				(window as any).io
			) {
				const io = (window as any).io;
				const newSocket = io(targetUrl, {
					transports: ['websocket', 'polling'],
				});

				newSocket.on('connect', () => {
					setIsConnected(true);
					setError(null);
				});

				newSocket.on('disconnect', () => {
					setIsConnected(false);
				});

				newSocket.on('error', (err: any) => {
					setError(
						err.message || 'Socket connection error'
					);
				});

				setSocket(newSocket);
			} else {
				// Fallback to basic WebSocket
				const wsUrl = targetUrl.replace(
					'/api/socket',
					'/api/ws'
				);
				const newSocket = new WebSocket(wsUrl);

				newSocket.onopen = () => {
					setIsConnected(true);
					setError(null);
				};

				newSocket.onclose = () => {
					setIsConnected(false);
				};

				newSocket.onerror = () => {
					setError('WebSocket connection error');
				};

				setSocket(newSocket);
			}
		} catch (err: any) {
			setError(err.message || 'Failed to connect');
		}
	};

	const disconnect = () => {
		if (socket) {
			if (typeof socket.disconnect === 'function') {
				socket.disconnect(); // Socket.IO
			} else if (typeof socket.close === 'function') {
				socket.close(); // WebSocket
			}
			setSocket(null);
			setIsConnected(false);
		}
	};

	const emit = (event: string, data?: any) => {
		if (socket && isConnected) {
			if (typeof socket.emit === 'function') {
				socket.emit(event, data); // Socket.IO
			} else if (socket.send) {
				socket.send(JSON.stringify({ event, data })); // WebSocket
			}
		}
	};

	useEffect(() => {
		if (autoConnect && session) {
			connect();
		}

		return () => {
			disconnect();
		};
	}, [session, autoConnect]);

	return (
		<SocketContext.Provider
			value={{
				socket,
				isConnected,
				error,
				connect,
				disconnect,
				emit,
			}}
		>
			{children}
		</SocketContext.Provider>
	);
};

export default SocketProvider;
